/*
 *  Footer
 *    Styles for the footer section
 *
 *  Version:
 *    1.0.0 - 2021/08/25
 */

.c-footer {
  padding: 1rem $s-gutter--edge-mobile 0;
  background-color: $s-color--background-focus;

  // Brand messaging
  &__brand {
    &-container {
      padding-right: 1.1875rem;
      margin-bottom: 3.5625rem;
    }

    &-message {
      margin-bottom: 2rem;
    }

    &-image {
      max-width: 20rem;

      &.is-desktop { display: none; }
    }
  }

  // Columns
  &__columns {
    margin-bottom: 1.25rem;
  }

  &__feature-text {
    margin-bottom: 2.5rem;
  }

  // Subscribe & Socials
  &__subscribe-socials {
    margin-bottom: 1.4375rem;
  }

  // Subscribe
  &__subscribe {
    margin-bottom: 2.875rem;

    &-title {
      margin-bottom: 0.75rem;
    }

    &-subtitle {
      margin-bottom: 1.25rem;
    }
  }

  // Socials
  &__socials {
    display: flex;
  }

  &__social {
    &-link {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 1.5rem;
      height: 1.5rem;
      margin-left: 2.125rem;

      &:first-child {
        margin-left: -0.5rem;
      }
    }

    &-icon {
      &.o-icon--facebook {
        width: 0.5rem;
        height: 1rem;
      }

      &.o-icon--instagram {
        width: 0.875rem;
        height: 0.875rem;
      }

      &.o-icon--twitter {
        width: 1rem;
        height: 0.8125rem;
      }

      &.o-icon--pinterest {
        width: 0.75rem;
        height: 1rem;
      }
    }
  }

  // Menu
  &__menu {
    &-column {
      border-top: 0.0625rem solid $s-color--border-subdued-20;
      overflow: hidden;

      &:first-child {
        border-top: 0;
      }
    }
  }

  &__heading {
    position: relative;
    padding: 1.5rem 0;
    user-select: none;
    list-style: none;

    &::-webkit-details-marker {
      display: none;
    }

    &::after {
      @include t-anim--transition(transform, $s-anim--time-short, linear);
      @include t-absolute-center-y;
      right: 0;
      display: block;
      width: 0.75rem;
      height: 0.5rem;
      background: url(../../assets/icon-chevron-down.svg) no-repeat center center;
      content: "";

      [open] & {
        transform: translateY(-50%) rotate(-90deg);
      }
    }
  }

  &__links {
    list-style: none;
    padding: 0 0 1.5rem;
    margin: 0;
  }

  // Copyright and attribution
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1.4375rem;
  }

  &__copyright, &__process {
    color: $s-color--font-subdued-20;
  }

  &__copyright {
    margin-bottom: 0;
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__brand {
      &-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 0.1875rem;
      }

      &-message {
        margin: 0 1.25rem 0 0;
        max-width: 46.875rem;
      }
    }

    &__subscribe {
      &-socials {
        max-width: 26.25rem;
        margin-bottom: 0;
      }

      &-title {
        margin-bottom: 1.125rem;
      }
    }

    &__columns {
      display: flex;
      justify-content: space-between;
      flex-direction: row-reverse;
      margin-bottom: 2.5rem;
    }

    &__menu {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 56.25rem;
      margin-right: 1rem;

      &-column {
        border-top: 0;
        width: 33.33%;
      }

      &-item {
        margin-bottom: 0.25rem;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &__heading {
      @include t-font--heading-4;
      font-family: $s-font--heading-family;
      padding: 0;
      margin-bottom: 1.125rem;

      &::after {
        display: none;
      }
    }

    &__links {
      padding-bottom: 0;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 1.875rem 0 0;

    &__inner {
      padding: 0 6%;
      max-width: $s-page-wrap--medium;
      margin: 0 auto;
    }

    &__bottom {
      padding: 0 2.0938rem 2.0625rem;
    }

    &__brand {
      &-container {
        margin-bottom: 5.625rem;
      }

      &-image {
        width: 45.54%;
        max-width: 41.5625rem;

        &.is-mobile { display: none; }
        &.is-desktop { display: block; }
      }
    }

    &__columns {
      margin-bottom: 3.5rem;
    }

    &__heading {
      @include t-font--heading-4-laptop;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding-top: 6.875rem;

    &__inner {
      padding-left: $s-gutter--edge-large;
      padding-right: $s-gutter--edge-large;
      max-width: unset;
    }

    &__bottom {
      padding: 0 3.25rem 2.5rem 2.9375rem;
    }

    &__brand {
      &-container {
        margin-bottom: 8.5rem;
      }

      &-message {
        margin-right: 2.5rem;
      }

      &-image {
        transform: translateY(-1rem);
      }
    }

    &__subscribe {
      margin-bottom: 4.1875rem;

      &-socials {
        max-width: 25.625rem;
        width: 100%;
      }

      &-title {
        margin-bottom: 2.25rem;
      }

      &-subtitle {
        margin-bottom: 1.5rem;
      }
    }

    &__social-link {
      margin-left: 1.5625rem;
    }

    &__columns {
      margin-bottom: 5rem;
    }

    &__heading {
      @include t-font--heading-4-desktop;
      margin-bottom: 2.25rem;
    }

    &__menu {
      &-item {
        margin-bottom: 0.5rem
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__inner {
      max-width: 125rem;
    }

    &__bottom {
      max-width: 125rem;
      margin: 0 auto;
    }
  }
}